* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  height: 600px;
  background-color: #a40606;
  background-image: linear-gradient(315deg, #a40606 0%, #d98324 74%);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-shadow: 0px 12px 18px -4px rgba(0, 0, 0, 0.3);
}

.upper_wrapper {
  display: flex;
  margin-bottom: 20px;
}

.lower_wrapper {
  display: flex;
  width: 100%;
}

.app__header {
  width: 100%;
  padding: 10px;
  margin-top: -10px;
  text-align: center;
  color: #fff;
}
